/**
 * @module body
 */
body {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  background-color: #fff;
  margin-top: 70px;
}
.item-label,
.text-info {
  opacity: 0.5;
}
/**
 * @module debug
 */
/**
 * @module dev
 */
.TRACE {
  border: 1px solid red;
}
.HIDDEN {
  display: none;
}
#DIM {
  overflow: hidden;
  line-height: 150%;
  z-index: 9500;
  position: fixed;
  bottom: 0;
  left: 20px;
  font-family: Lucida Console, monospace;
  font-size: 8pt;
  white-space: pre;
  background: rgba(0, 0, 0, 0.8);
  color: rgba(200, 200, 200, 0.8);
  padding: 5px;
  padding-right: 20px;
  margin: 10px auto;
  border-radius: 6px;
}
#DIM span {
  color: #555555;
}
#DIM:before {
  content: "-";
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.15);
  padding-left: 8px;
  padding-right: 8px;
  margin-left: 20px;
  margin-right: 20px;
  border-radius: 4px;
}
@media (min-width: 480px) {
  #DIM:before {
    content: "xs 480px";
  }
}
@media (min-width: 768px) {
  #DIM:before {
    content: "sm 768px";
  }
}
@media (min-width: 992px) {
  #DIM:before {
    content: "md 992px";
  }
}
@media (min-width: 1200px) {
  #DIM:before {
    content: "lg 1200px";
  }
}
#less-error-message-static {
  margin-top: 100px;
}
/**
 * @module main-page-splash
 */
.main-page-splash {
  background-color: #486;
  color: #fff;
  position: relative;
}
.main-page-splash:before {
  display: block;
  content: ' ';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  opacity: 0.15;
  background-image: url('/static/images/splash/curves-bg/curves-x.svg');
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
}
.main-page-splash > .content {
  position: relative;
}
.main-page-splash > .content h1 {
  font-weight: 400;
}
/**
 * @module page-footer-navbar
 */
.page-footer-navbar {
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  margin-bottom: 0;
  margin-top: 20px;
}
.page-footer-navbar .container-fluid {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
  justify-content: space-between;
}
.page-footer-navbar .container-fluid .navbar-right {
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .page-footer-navbar .container-fluid {
    flex-direction: row;
  }
  .page-footer-navbar .container-fluid .navbar-copyright,
  .page-footer-navbar .container-fluid .navbar-middle,
  .page-footer-navbar .container-fluid .navbar-right {
    flex: 1;
  }
  .page-footer-navbar .container-fluid .navbar-right {
    justify-content: flex-end;
  }
}
.page-footer-navbar .navbar-copyright {
  display: flex;
  align-items: center;
}
/**
 * @module page-header-navbar
 * @changed 2024.02.27, 22:10
 */
.page-header-navbar.navbar-inverse.navbar-primary {
